const REGULAR_HOURS_PER_DAY = 8;
const DAY_NAMES = ['ä¸€', 'äºŒ', 'ä¸‰', 'å››', 'äº”', 'å…­', 'æ—¥'];
const STATE = {
  OFF: 0,
  REGULAR_WORK: 1,
  OVER_TWO_HOURS_WORK: 2,
  OVER_THREE_HOURS_WORK: 3,
  DAYOFF_WORK: 4
};

function current (workhours, hourlyPay) {
  let workingMatrix = [];
  let total = 0;
  let overtimeHours = 0;

  workhours.forEach((workhour, dayOfWeek) => {
    let workday = Array.apply(null, Array(12)).map((val, i) => {
      let currentState = STATE.OFF;

      if (workhour > i) {
        total++;
      }

      if (workhour <= i) {
        currentState = STATE.OFF;
      } else if (dayOfWeek === 6) {
        currentState = STATE.DAYOFF_WORK;
      } else if (total - overtimeHours > 42) {
        currentState = STATE.OVER_THREE_HOURS_WORK;
        overtimeHours++;
      } else if (total - overtimeHours > 40) {
        currentState = STATE.OVER_TWO_HOURS_WORK;
        overtimeHours++;
      } else if (i < REGULAR_HOURS_PER_DAY) {
        currentState = STATE.REGULAR_WORK;
      } else if (i - REGULAR_HOURS_PER_DAY >= 2) {
        currentState = STATE.OVER_THREE_HOURS_WORK;
        overtimeHours++;
      } else if (i - REGULAR_HOURS_PER_DAY < 2) {
        currentState = STATE.OVER_TWO_HOURS_WORK;
        overtimeHours++;
      } else {
        currentState = STATE.OFF;
      }

      return currentState;
    });
    workingMatrix.push(workday);
  });
  var transposed = workingMatrix[0].map(function (col, i) {
    return workingMatrix.map(function (row) {
      return row[i];
    });
  });

  let pay = 0;
  workingMatrix.forEach(day => {
    day.forEach(hour => {
      if (hour === 2) {
        pay += hourlyPay * 4 / 3;
      } else if (hour === 3) {
        pay += hourlyPay * 5 / 3;
      }
    });
  });

  // é€±æ—¥å·¥ä½œçš„è–ªè³‡è¦å‰‡ï¼Œç‚ºä»€éº¼æå¾—é€™éº¼è¤‡é›œï¼Ÿ
  // ä¾‹å‡æ—¥å·¥ä½œå…«å€‹å°æ™‚ä»¥å…§ï¼Œè–ªæ°´çš†ä»¥ 150 * 8 è¨ˆç®—
  // è¶…éå…«å€‹å°æ™‚çš„å‰å…©å€‹å°æ™‚ï¼Œè–ªæ°´ä»¥ 150 * 4 / 3 * n è¨ˆç®—
  // è¶…åœ‹åå°æ™‚ï¼Œè–ªæ°´ä»¥ 150 * 5 / 3 * n è¨ˆç®—
  if (workhours[6] > 0 && workhours[6] <= 8) {
    pay += hourlyPay * 8;
  } else if (workhours[6] - 10 > 0) {
    pay += (workhours[6] - 10) * 150 * 5 / 3 +
            2 * 150 * 4 / 3 +
            hourlyPay * 8;
  } else if (workhours[6] - 8 > 0) {
    pay += (workhours[6] - 8) * 150 * 4 / 3 + hourlyPay * 8;
  }

  return {
    workingMatrix: workingMatrix,
    transposed: transposed,
    overtimePay: pay
  };
}

function oneRestOneOff (workhours, hourlyPay) {
  let workingMatrix = [];
  let total = 0;
  let overtimeHours = 0;

  workhours.forEach((workhour, dayOfWeek) => {
    let workday = Array.apply(null, Array(12)).map((val, i) => {
      let currentState = STATE.OFF;

      if (workhour > i) {
        total++;
      }

      if (workhour <= i) {
        currentState = STATE.OFF;
      } else if (dayOfWeek === 6) {
        currentState = STATE.DAYOFF_WORK;
      } else if (total - overtimeHours > 42) {
        currentState = STATE.OVER_THREE_HOURS_WORK;
        overtimeHours++;
      } else if (total - overtimeHours > 40) {
        currentState = STATE.OVER_TWO_HOURS_WORK;
        overtimeHours++;
      } else if (i < REGULAR_HOURS_PER_DAY) {
        currentState = STATE.REGULAR_WORK;
      } else if (i - REGULAR_HOURS_PER_DAY >= 2) {
        currentState = STATE.OVER_THREE_HOURS_WORK;
        overtimeHours++;
      } else if (i - REGULAR_HOURS_PER_DAY < 2) {
        currentState = STATE.OVER_TWO_HOURS_WORK;
        overtimeHours++;
      } else {
        currentState = STATE.OFF;
      }

      return currentState;
    });
    workingMatrix.push(workday);
  });
  var transposed = workingMatrix[0].map(function (col, i) {
    return workingMatrix.map(function (row) {
      return row[i];
    });
  });

  let pay = 0;
  workingMatrix.forEach((day, i) => {
    if (i === 5) {
      return;
    }

    day.forEach(hour => {
      if (hour === 2) {
        pay += hourlyPay * 4 / 3;
      } else if (hour === 3) {
        pay += hourlyPay * 5 / 3;
      }
    });
  });

  // http://www.cna.com.tw/news/firstnews/201606290106-1.aspx
  // æ ¹æ“šå‹å‹•éƒ¨è³‡æ–™ï¼Œä¼‘æ¯æ—¥å·¥è³‡è¨ˆç®—çš„éƒ¨åˆ†ï¼Œæ“¬å¾åŸæœ¬çš„åŠ å€ç™¼çµ¦ï¼Œæ”¹ç‚ºåœ¨2å°æ™‚ä»¥å…§è€…ï¼ŒæŒ‰å¹³æ—¥å·¥è³‡
  // é¡å¦çµ¦äºˆæ¯å°æ™‚1åˆ1/3ï¼Œå†ç¹¼çºŒå·¥ä½œè€…å¦çµ¦äºˆæ¯å°æ™‚1åˆ2/3ã€‚å·¥ä½œæ™‚é–“è¨ˆç®—æ–¹å¼ç‚ºå·¥ä½œ4å°æ™‚ä»¥å…§
  // ï¼Œä»¥4å°æ™‚è¨ˆç®—ï¼Œè¶…é4å°æ™‚è‡³8å°æ™‚ï¼Œä»¥8å°æ™‚è¨ˆç®—ï¼Œè¶…é8å°æ™‚è‡³12å°æ™‚ä»¥å…§è€…ï¼Œä»¥12å°æ™‚è¨ˆã€‚

  if (workhours[5] > 0) {
    if (workhours[5] <= 4) {
      pay += hourlyPay * 2 * 7 / 3 + hourlyPay * 2 * 8 / 3;
    } else if (workhours[5] <= 8) {
      pay += hourlyPay * 2 * 7 / 3 + hourlyPay * 6 * 8 / 3;
    } else {
      pay += hourlyPay * 2 * 7 / 3 + hourlyPay * 10 * 8 / 3;
    }
  }

  // é€±æ—¥å·¥ä½œçš„è–ªè³‡è¦å‰‡ï¼Œç‚ºä»€éº¼æå¾—é€™éº¼è¤‡é›œï¼Ÿ
  // ä¾‹å‡æ—¥å·¥ä½œå…«å€‹å°æ™‚ä»¥å…§ï¼Œè–ªæ°´çš†ä»¥ 150 * 8 è¨ˆç®—
  // è¶…éå…«å€‹å°æ™‚çš„å‰å…©å€‹å°æ™‚ï¼Œè–ªæ°´ä»¥ 150 * 4 / 3 * n è¨ˆç®—
  // è¶…åœ‹åå°æ™‚ï¼Œè–ªæ°´ä»¥ 150 * 5 / 3 * n è¨ˆç®—
  if (workhours[6] > 0 && workhours[6] <= 8) {
    pay += hourlyPay * 8;
  } else if (workhours[6] - 10 > 0) {
    pay += (workhours[6] - 10) * 150 * 5 / 3 +
            2 * 150 * 4 / 3 +
            hourlyPay * 8;
  } else if (workhours[6] - 8 > 0) {
    pay += (workhours[6] - 8) * 150 * 4 / 3 + hourlyPay * 8;
  }

  return {
    workingMatrix: workingMatrix,
    transposed: transposed,
    overtimePay: pay
  };
}

function twoOff (workhours, hourlyPay) {
  let workingMatrix = [];
  let total = 0;
  let overtimeHours = 0;

  workhours.forEach((workhour, dayOfWeek) => {
    let workday = Array.apply(null, Array(12)).map((val, i) => {
      let currentState = STATE.OFF;

      if (workhour > i) {
        total++;
      }

      if (workhour <= i) {
        currentState = STATE.OFF;
      } else if (dayOfWeek === 5 || dayOfWeek === 6) {
        currentState = STATE.DAYOFF_WORK;
      } else if (total - overtimeHours > 42) {
        currentState = STATE.OVER_THREE_HOURS_WORK;
        overtimeHours++;
      } else if (total - overtimeHours > 40) {
        currentState = STATE.OVER_TWO_HOURS_WORK;
        overtimeHours++;
      } else if (i < REGULAR_HOURS_PER_DAY) {
        currentState = STATE.REGULAR_WORK;
      } else if (i - REGULAR_HOURS_PER_DAY >= 2) {
        currentState = STATE.OVER_THREE_HOURS_WORK;
        overtimeHours++;
      } else if (i - REGULAR_HOURS_PER_DAY < 2) {
        currentState = STATE.OVER_TWO_HOURS_WORK;
        overtimeHours++;
      } else {
        currentState = STATE.OFF;
      }

      return currentState;
    });
    workingMatrix.push(workday);
  });
  var transposed = workingMatrix[0].map(function (col, i) {
    return workingMatrix.map(function (row) {
      return row[i];
    });
  });

  let pay = 0;
  workingMatrix.forEach(day => {
    day.forEach(hour => {
      if (hour === 2) {
        pay += hourlyPay * 4 / 3;
      } else if (hour === 3) {
        pay += hourlyPay * 5 / 3;
      }
    });
  });

  // é€±å…­èˆ‡é€±æ—¥å·¥ä½œçš„è–ªè³‡è¦å‰‡
  // ä¾‹å‡æ—¥å·¥ä½œå…«å€‹å°æ™‚ä»¥å…§ï¼Œè–ªæ°´çš†ä»¥ 150 * 8 è¨ˆç®—
  // è¶…éå…«å€‹å°æ™‚çš„å‰å…©å€‹å°æ™‚ï¼Œè–ªæ°´ä»¥ 150 * 4 / 3 * n è¨ˆç®—
  // è¶…åœ‹åå°æ™‚ï¼Œè–ªæ°´ä»¥ 150 * 5 / 3 * n è¨ˆç®—
  [5, 6].forEach(day => {
    if (workhours[day] > 0 && workhours[day] <= 8) {
      pay += hourlyPay * 8;
    } else if (workhours[day] - 10 > 0) {
      pay += (workhours[day] - 10) * 150 * 5 / 3 +
              2 * 150 * 4 / 3 +
              hourlyPay * 8;
    } else if (workhours[day] - 8 > 0) {
      pay += (workhours[day] - 8) * 150 * 4 / 3 + hourlyPay * 8;
    }
  });

  return {
    workingMatrix: workingMatrix,
    transposed: transposed,
    overtimePay: pay
  };
}

export {
  REGULAR_HOURS_PER_DAY,
  DAY_NAMES,
  STATE,
  current,
  oneRestOneOff,
  twoOff
};



